.ant-layout-content {
  margin-top: 64px;
  padding: 0px 50px;
}

.ant-row {
  margin-top: 20px;
}

.ant-card-body .ant-row {
  margin-top: 0px;
}

.ant-card-head-title h1 {
  margin-bottom: 0px;
}

.ant-card-head-title h2 {
  margin-bottom: 0px;
}

.ChartWrapper {
  padding: 10px;
}

.ChartWrapper__title__unit {
  font-size: 80%;
}

.ChartWrapper__title {
  font-weight: bold;
  font-size: 110%;
}

.ChartWrapper__header {
  display: flex;
  justify-content: space-between;
}

.ChartWrapper__header__right {
  display: flex;
}

.ChartWrapper__footer {
  font-size: 70%;
  text-align: right;
}

.StatisticDetails__sources {
  margin-bottom: 0px;
  padding-left: 10px;
}

.CustomTooltip {
  background-color: white;
  border: 1px solid #cccccc;
  padding: 10px;
  font-size: 11;
}

.CustomTooltip ul {
  list-style-type: none;
}

.AppLayout__splash {
  height: 80vh;
}

.HomeView .leaflet-container {
  /* background-color: rgb(253, 248, 222); */
  background-color: white;
}

.WorldMap__yearLabel {
  position: absolute;
  right: 10px;
  background-color: white;
  z-index: 1;
  padding: 5px;
  font-weight: bold;
  border-radius: 0 0 0 3px;
}

.WorldMap__legend {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.WorldMap__legend__colors {
  display: flex;
}

.WorldMap__legend__gradient {
  display: flex;
  margin-right: 10px;
}

.WorldMap__legend__gradient__item {
  height: 20px;
}

.WorldMap__legend__NA__square {
  height: 20px;
  width: 20px;
}

.WorldMap__legend__gradientRange {
  display: flex;
  justify-content: space-between;
  width: 300px;
}

.WorldMap__sources {
  margin-top: 10px;
  font-size: 70%;
  text-align: right;
}

.StatisticExplorer__options {
  display: flex;
  justify-content: space-between;
}

.StatisticExplorer__options__right {
  display: flex;
}

.SettingComponent {
  margin-bottom: 20px;
}

#mainCountrySelect {
  width: 20em;
}

.toPng {
  display: none;
}

.transformingToPng .toPng {
  display: block;
}

.EnergyMixChart .recharts-legend-item {
  cursor: pointer;
  margin: 1px;
  padding: 2px 5px;
  border: solid 1px rgb(182, 182, 182);
  border-radius: 2px;
  -webkit-user-select: none; /* webkit (safari, chrome) browsers */
  -moz-user-select: none; /* mozilla browsers */
  -khtml-user-select: none; /* webkit (konqueror) browsers */
  -ms-user-select: none; /* IE10+ */
}

.recharts-legend-item.inactive {
  background-color: #ebe8e8;
  color: #ccc;
}

.transformingToPng .recharts-legend-item {
  border: none;
}

.transformingToPng .recharts-legend-item.inactive {
  display: none !important;
}

@media (max-width: 640px) {
  .AppLayout .ant-layout-header {
    padding: 0px 0px;
  }

  .ant-layout-content {
    padding: 0px 10px;
  }

  .hideOnMobile {
    display: none;
  }

  #mainCountrySelect {
    width: 10em;
  }
}
